@use "../../styles/_variables.scss" as *;
@use "../../styles/_reset.scss" as *;
@use "../../styles/_typography.scss" as *;

.about-me-layout {
    height: 100vh;
    width: 100vw;
}

@media only screen and (max-width: $mobile-breakpoint) {
    .about-me-layout {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
}